/* Global resets */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow: hidden;
}

/* Set a basic font and background color */
* {
  font-family: 'Aldrich', sans-serif; /* Unified font family */
  background-color: #0B1215;
  color: #FFFFFF; /* Unified color */
}

/* Set default styles for app container */
.App {
  text-align: center;
  padding: 20px;
}

/* Style header and logo */
.App-header {
  background-color: #0B1215;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  font-size: calc(10px + 2vmin);
  color: #FFFFFF;
  
}

.App-logo {
  height: 20vmin; /* Adjust the height as needed */
  max-width: 100%; /* Ensure it doesn't exceed the container */
  pointer-events: none;
  
}

/* Style links */
a {
  text-decoration: none; /* Remove underline */
  color: #007bff; /* Set link color */
}

a:hover {
  text-decoration: underline; /* Underline on hover */
}

/* Hide content in portrait mode */
@media screen and (orientation: portrait) {
  .orientation-message {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #0b1215;
    color: #fff;
    text-align: center;
    font-size: 1.5em;
    padding: 20px;
    box-sizing: border-box;
  }

  .main-content {
    display: none;
  }
}

/* Show content in landscape mode */
@media screen and (orientation: landscape) {
  .orientation-message {
    display: none;
  }

  .main-content {
    display: block;
  }
}

/* Add more global styles as needed */
.Player-icon {
  position: fixed;
  top: 35px;
  right: 15px;

}